import React, { useState, useEffect } from "react";
// import { getArrayFromText } from "../../_utils/Misc";
import { useSpring, animated } from "react-spring";
import { Text } from "../ui/elements";
import { useDispatch } from "react-redux";
import { setChoicesActive } from "../../_redux/slices/utilsSlice";

export const TextBringer = ({ text, maxLength = 20, callback }) => {
  const dispatch = useDispatch();
  const textArr = text;

  const [current, setCurrent] = useState(0);
  const [animate, setAnimate] = useState(true);

  const next = () => {
    if (current + 1 >= textArr.length) {
      dispatch(setChoicesActive(true));
      callback && callback();
      return;
    }
    setAnimate(false);
    setTimeout(() => {
      setCurrent(current + 1);
      setAnimate(true);
    }, 500);
  };

  dispatch(setChoicesActive(false));

  const currentPlus = current + 1;

  const contentProps = useSpring({
    opacity: animate ? 1 : 0,
    marginTop: animate ? 0 : -100
  });

  useEffect(() => {
    setTimeout(() => {
      setAnimate(true);
    }, 1500);
  }, [dispatch]);

  return (
    <div className="TextBringer" onClick={next}>
      {currentPlus && (
        <animated.div style={contentProps}>
          <Text>{textArr[current]}</Text>
        </animated.div>
      )}
    </div>
  );
};
