import React from "react";
import * as scenes from "./scenes";
// import { SceneA, SceneAC, SceneAC2 } from "../components/scenes/scenes";
import { Route, withRouter } from "react-router-dom";
export const SceneRouteCreator = withRouter(() => {
  const routes = [];
  for (const s in scenes) {
    routes.push({
      path: `/${s}`
        .split("")
        .map((el, idx) => (idx === 1 ? el.toUpperCase() : el))
        .join(""),
      component: scenes[s]
    });
  }

  return (
    <>
      {routes.map(({ path, component }, key) => (
        <Route path={path} key={key} component={component} />
      ))}
    </>
  );
});
