import React, { useState, useRef } from "react";
import styled from "styled-components";
import { SceneTpl } from "../../SceneTpl";
import bg from "../../../../scene-materials/a/stranger2.jpg";
import { TextBringer } from "../../../TextBringer/TextBringer";
// import { ChoicesNav } from "../../ChoicesNav";
import sound from "../../../../sounds/scene-2/stalker.mp3";
import { withSceneF } from "../../../../_hoc/withSceneF";
import { Input, NavLink } from "../../../ui/elements";
import { withRouter } from "react-router-dom";

export const SceneAB = withRouter(
  withSceneF(sound)(
    styled(({ className, history }) => {
      const [done, setDone] = useState(false);
      const [input, setInput] = useState("");
      const form = useRef(null);
      // useEffect(() => () => setDone(false));
      const onSubmit = e => {
        e.preventDefault();
        if (input.trim().toLowerCase() === "андронио санрайз") {
          history.push("/sceneAB2");
        } else {
          history.push("/sceneAB3");
        }
      };

      return (
        <div className={`Scene ${className}`}>
          {/* <SceneLoader /> */}
          <SceneTpl bg={bg}>
            <TextBringer
              callback={() => setDone(true)}
              text={[
                `Пройдя порядка 2 километров пернатый увидел фигуру вдалеке. Неизвестно по каким причинам, пернатый решил что этот человек его спасение`,
                `Добрый день, ээм, меня зовут пернатый, я заблудился и честно говоря ничего не помню. Подскажите как выйти к городу`,
                `Здравствуй, как говоришь тебя зовут?`
              ]}
            />
            {done && (
              <form onSubmit={onSubmit} ref={form}>
                <Input
                  placeholder="Ваше имя"
                  value={input}
                  onChange={e => setInput(e.target.value)}
                />
                <button
                  type="submit"
                  style={{
                    background: "transparent",
                    marginTop: "2rem",
                    border: "none"
                  }}
                >
                  <NavLink isAppearance>Сказать</NavLink>
                </button>
              </form>
            )}
            {/* <ChoicesNav choices={choices}></ChoicesNav> */}
          </SceneTpl>
        </div>
      );
    })``
  )
);
