import { createSlice } from "@reduxjs/toolkit";

export const slice = createSlice({
  name: "sound",
  initialState: {
    muted: localStorage.getItem("muted") === "false" ? false : true,
    currentTheme: null,
    currentVoice: null
  },
  reducers: {
    setMuted: (state, action) => {
      localStorage.setItem("muted", action.payload);
      state.muted = action.payload;
    },
    setCurrentTheme: (state, action) => {
      state.currentTheme = action.payload;
    },
    setCurrentVoice: (state, action) => {
      state.currentVoice = action.payload;
    }
  }
});

export const { setMuted, setCurrentTheme, setCurrentVoice } = slice.actions;

export const selectMuted = state => state.sound.muted;
export const selectCurrentTheme = state => state.sound.currentTheme;
export const selectCurrentVoice = state => state.sound.currentVoice;

export default slice.reducer;
