import React from "react";
import styled from "styled-components";
import { SceneTpl } from "../../SceneTpl";
// import bg from "../../../../img/scene-3/bg2.jpg";
// import { TextBringer } from "../../../TextBringer/TextBringer";
// import { ChoicesNav } from "../../ChoicesNav";
import stalker from "../../../../sounds/scene-2/stalker.mp3";
import { withSceneF } from "../../../../_hoc/withSceneF";
// import { ReactRunner } from "../../../ReactRunner/ReactRunner";
import ReactPlayer from "react-player";
import { useSelector } from "react-redux";
import { selectMuted } from "../../../../_redux/slices/soundSlice";
import { withRouter } from "react-router-dom";

export const SceneAB3 = withRouter(
  withSceneF(stalker)(
    styled(({ className, history }) => {
      const muted = useSelector(selectMuted);

      return (
        <div className={`Scene ${className}`}>
          {/* <SceneLoader /> */}
          <SceneTpl bg={null}>
            <div className="video-overlay"></div>
            <ReactPlayer
              url="https://youtu.be/g_GNCWWipJ0?t=59"
              playing
              className="video"
              width="100%"
              height="100%"
              controls={false}
              muted={muted}
              onProgress={e => {
                // console.log(e.playedSeconds);
                if (Math.round(e.playedSeconds) === 72) {
                  history.push("/");
                }
              }}
            />
            {/* {visible && <ReactRunner success={success} fail={fail} />} */}
          </SceneTpl>
        </div>
      );
    })``
  )
);
