import React, { useEffect } from "react";
// import { useSelector } from "react-redux";
// import { selectMuted } from "../_redux/slices/soundSlice";
import { useDispatch } from "react-redux";
import { setCurrentTheme, setCurrentVoice } from "../_redux/slices/soundSlice";

export const withSceneF = (
  currentTheme = null,
  currentVoice = null
) => Component => props => {
  const dispatch = useDispatch();
  dispatch(setCurrentTheme(currentTheme));
  dispatch(setCurrentVoice(currentVoice));

  useEffect(() => {
    return () => dispatch(setCurrentVoice(null));
  }, [dispatch]);
  return <Component {...props} />;
};
