import { createSlice } from "@reduxjs/toolkit";

export const slice = createSlice({
  name: "utils",
  initialState: {
    fontsLoaded: true,
    appInited: false,
    choicesActive: false
  },
  reducers: {
    setFontsLoaded: (state, action) => {
      state.fontsLoaded = action.payload;
    },
    setAppInited: (state, action) => {
      state.appInited = action.payload;
    },
    setChoicesActive: (state, action) => {
      state.choicesActive = action.payload;
    }
  }
});

export const { setFontsLoaded, setAppInited, setChoicesActive } = slice.actions;

export const selectFontsLoaded = state => state.utils.fontsLoaded;
export const selectAppInited = state => state.utils.appInited;
export const selectChoicesActive = state => state.utils.choicesActive;

export default slice.reducer;
