import React from "react";
import styled from "styled-components";
import { Scene1 } from "./components/scenes/Scene1";
import { withRouter, Switch, Route } from "react-router-dom";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import { SoundManager } from "./components/sound/SoundManager";
import { SceneLoader } from "./components/scenes/SceneLoader";
import { SceneRouteCreator } from "./components/scenes/SceneRouteCreator";
// import { ProgressBar } from "./components/ui/modules/ProgressBar";

import { useSelector } from "react-redux";
import { selectAppInited } from "./_redux/slices/utilsSlice";

function App({ location, className }) {
  const appInited = useSelector(selectAppInited);

  return (
    <div className={`App ${className}`}>
      <SceneLoader />
      {appInited && (
        <>
          <SoundManager />
          <TransitionGroup>
            <CSSTransition
              key={location.key}
              classNames="route-swipes"
              timeout={4500}
              unmountOnExit={true}
            >
              <div className="swipe-container">
                <Switch>
                  <Route path="/" exact>
                    <Scene1 />
                  </Route>
                  <SceneRouteCreator />
                </Switch>
              </div>
            </CSSTransition>
          </TransitionGroup>
        </>
      )}
    </div>
  );
}

export default withRouter(styled(App)`
  .route-swipes-enter {
    opacity: 0;
  }
  .route-swipes-enter-active {
    opacity: 1;
    transition: all 4.5s;
  }
  .route-swipes-exit {
    opacity: 1;
  }
  .route-swipes-exit-active {
    opacity: 1;
    transition: all 0.5s;
  }
`);
