import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { SceneTpl } from "../../SceneTpl";
// import bg from "../../../../img/scene-3/bg2.jpg";
// import { TextBringer } from "../../../TextBringer/TextBringer";
// import { ChoicesNav } from "../../ChoicesNav";
import stalker from "../../../../sounds/scene-2/stalker.mp3";
import { withSceneF } from "../../../../_hoc/withSceneF";
import { ReactRunner } from "../../../ReactRunner/ReactRunner";
import ReactPlayer from "react-player";
import { useSelector } from "react-redux";
import { selectMuted } from "../../../../_redux/slices/soundSlice";
import { withRouter } from "react-router-dom";

export const SceneAA = withRouter(
  withSceneF(stalker)(
    styled(({ className, history }) => {
      const muted = useSelector(selectMuted);
      const [visible, setVisible] = useState(false);

      useEffect(() => {
        let timeout = setTimeout(() => {
          setVisible(true);
        }, 4000);

        return () => clearTimeout(timeout);
      });

      const success = () => {
        history.push("/sceneAA2");
      };
      const fail = () => {
        history.push("/sceneAAX");
      };

      return (
        <div className={`Scene ${className}`}>
          {/* <SceneLoader /> */}
          <SceneTpl bg={null}>
            <div className="video-overlay"></div>
            <ReactPlayer
              url="https://youtu.be/FOceDBYL82k?t=124"
              playing
              className="video"
              width="100%"
              height="100%"
              controls={false}
              muted={muted}
              onProgress={e => {
                // console.log(e.playedSeconds);
                if (Math.round(e.playedSeconds) === 152) {
                  // history.push("/");
                }
              }}
            />
            {visible && <ReactRunner success={success} fail={fail} />}
          </SceneTpl>
        </div>
      );
    })`
      ${ReactRunner} {
        position: relative;
        z-index: 2;
      }
    `
  )
);
