import React, { useState } from "react";
import styled from "styled-components";
// import { CSSTransition, TransitionGroup } from "react-transition-group";
import { useSpring, animated } from "react-spring";
import { useDispatch } from "react-redux";
import { setAppInited } from "../../_redux/slices/utilsSlice";
// import FontFaceObserver from "fontfaceobserver";
// import { NavLink } from "../ui/elements";

export const SceneLoader = styled(({ className }) => {
  const dispatch = useDispatch();

  const [opacity, setOpacity] = useState(true);
  const [display, setDisplay] = useState(true);
  const [start, setStart] = useState(false);
  // const [visible, setVisible] = useState(false);

  // new FontFaceObserver("Amatic SC").load().then(
  //   function () {
  //     setTimeout(() => setVisible(true), 2000);
  //   },
  //   function () {
  //     console.log("Font is not available");
  //   }
  // );

  if (start) {
    dispatch(setAppInited(true));
    setTimeout(() => setOpacity(false), 500);
    setTimeout(() => setDisplay(false), 1500);
  }

  const props = useSpring({
    opacity: opacity ? 1 : 0,
    display: display ? "block" : "none"
  });

  return (
    <animated.div
      style={props}
      className={className}
      onClick={() => setStart(true)}
    >
      <div className="click">
        {/* <div className="hidden">hidden</div> */}
        <h6>Click</h6>
        {/* <NavLink isAppearance>Click</NavLink> */}
      </div>
    </animated.div>
  );
})`
  position: fixed;
  width: 100%;
  .hidden {
    width: 0;
    height: 0;
    position: fixed;
    top: -1000px;
    // font-size: 0;
    left: -10px;
    z-index: -2;
  }
  .click {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10rem;
    cursor: pointer;
  }
  color: ${props => props.theme.colors.light};

  text-transform: uppercase;
  height: 100%;
  left: 0;
  z-index: 99;
  transition: 0.5s;
  top: 0;
  background-color: ${props => props.theme.colors.darkBg};
`;

