import { useSelector } from "react-redux";
import { selectMuted } from "../../../../_redux/slices/soundSlice";
import ReactPlayer from "react-player";
import { withRouter } from "react-router-dom";
import { withSceneF } from "../../../../_hoc/withSceneF";

export const SceneAC3 = withSceneF(stalker)(
  withRouter(
    styled(({ className, history }) => {
      const muted = useSelector(selectMuted);
      // const [visible, setVisible] = useState(true);

      return (
        <div className={`Scene ${className}`}>
          {/* <SceneLoader /> */}
          <SceneTpl bg={null}>
            <div className="video-overlay"></div>

            <ReactPlayer
              url="https://youtu.be/LJ8UoUA2_uE?t=167"
              playing
              className="video"
              width="100%"
              height="100%"
              controls={false}
              muted={muted}
              onProgress={e => {
                if (Math.round(e.playedSeconds) === 178) {
                  history.push("/");
                }
              }}
            />
          </SceneTpl>
        </div>
      );
    })``
  )
);
