import React, { useState } from "react";
import { Scene, SceneOverlay } from "../ui/modules";
import { Container } from "../ui/elements";
import { useSelector } from "react-redux";
import { selectFontsLoaded } from "../../_redux/slices/utilsSlice";
import { ParallaxComponent } from "./Parallax";

export const SceneTpl = ({ bg, children }) => {
  const [bgLoaded, setBgLoaded] = useState(false);
  const fontsLoaded = useSelector(selectFontsLoaded);

  const mediaLoaded = bgLoaded && fontsLoaded;

  return (
    <Scene>
      <SceneOverlay />
      <ParallaxComponent setBgLoaded={setBgLoaded} bg={bg} />
      {mediaLoaded && <Container className={`content`}>{children}</Container>}
    </Scene>
  );
};
