import React from "react";
import { SoundToggler } from "./SoundToggler";
import { useSelector } from "react-redux";
import {
  selectMuted,
  selectCurrentTheme
  // selectCurrentVoice
} from "../../_redux/slices/soundSlice";
import Sound from "react-sound";
import { selectAppInited } from "../../_redux/slices/utilsSlice";

export const SoundManager = () => {
  const muted = useSelector(selectMuted);
  const appInited = useSelector(selectAppInited);
  const currentTheme = useSelector(selectCurrentTheme);
  // const currentVoice = useSelector(selectCurrentVoice);

  const playing = !muted && appInited;

  return (
    <div>
      <SoundToggler />

      <Sound
        url={currentTheme || null}
        // onLoad={() => setSoundLoaded(true)}
        playStatus={playing && currentTheme ? "PLAYING" : "PAUSED"}
        volume={20}
        loop
      />

      {/* <Sound
        url={currentVoice || null}
        // onLoad={() => setSoundLoaded(true)}
        playStatus={playing && currentVoice ? "PLAYING" : "PAUSED"}
        volume={10}
        loop
      /> */}
    </div>
  );
};
