import styled, { css } from "styled-components";
import React from "react";
import { Link } from "react-router-dom";
import { SoundedBtn } from "../../sound/SoundedBtn";

export const NavLink = styled(
  ({ className, children, disabled, to, isAppearance }) => (
    <SoundedBtn disabled={disabled} className={className}>
      {disabled || isAppearance ? (
        <div>{children}</div>
      ) : (
        <Link to={to}>{children}</Link>
      )}
    </SoundedBtn>
  )
)`
  font-size: 4rem;
  text-transform: uppercase;
  letter-spacing: 5px;
  font-weight: bold;
  text-shadow: 6px 6px 0px rgba(0, 0, 0, 0.2);
  color: white;
  a {
    color: inherit;
  }
  border-bottom: 4px solid transparent;
  border-top: 4px solid transparent;
  cursor: pointer;
  transition: 0.4s;
  &:hover {
    border-bottom: 4px solid white;
  }
  ${props =>
    props.btn &&
    css`
      a {
        text-shadow: none;
        background-color: ${props => props.theme.colors.light};
        color: ${props => props.theme.colors.primaryText};
        display: flex;
        padding: 1rem 2rem;
      }
      &:hover {
        border-bottom: 4px solid transparent;
        transform: scale(1.05);
      }
    `}
  ${props =>
    props.small &&
    css`
      font-size: 1.8rem;
    `}
  ${props =>
    props.disabled &&
    css`
      &:hover {
        border-bottom: 4px solid transparent;
        cursor: default;
      }
      color: #999;
    `}
`;
