import React from "react";
import { RowNav } from "../ui/modules";
import { NavLink } from "../ui/elements";
import { useSelector } from "react-redux";
import { selectChoicesActive } from "../../_redux/slices/utilsSlice";

export const ChoicesNav = ({ choices }) => {
  const choicesActive = useSelector(selectChoicesActive);

  return (
    <RowNav className="ChoicesNav">
      {choicesActive &&
        choices.map(el => (
          <NavLink to={el.to} small btn key={el.text}>
            {el.text}
          </NavLink>
        ))}
    </RowNav>
  );
};
